<template>
  <div class="signup-form" v-loading="loading">
    <el-card shadow="never" class="signup-form-card">
      <el-form ref="form" :model="form" :rules="rules" label-position="top" size="medium" @submit.prevent.native="onSubmit('form')">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" placeholder=") "></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="form.email" placeholder=") man2sfix@man2sfix.com"></el-input>
          <el-checkbox v-model="form.emailRecive"> </el-checkbox>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="form.password" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label=" " prop="passwordConfirm">
          <el-input type="password" v-model="form.passwordConfirm" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label=" " prop="phone">
          <div class="item-phone">
            <el-input type="text" v-model="form.phone" placeholder=") 010-0000-0000"></el-input>
          </div>
          <el-checkbox v-model="form.phoneRecive">SMS </el-checkbox>
        </el-form-item>
        <el-form-item label="" class="relative-container">
          <el-upload list-type="picture" action="#" :auto-upload="false" :limit="1" :on-change="uploadProfileFile" :on-remove="removeProfileFile">
            <el-button size="small" plain></el-button>
            <div slot="tip" class="el-upload__tip">1MB  gif/jpg/png  .</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="terms" class="relative-container">
          <el-checkbox v-model="form.terms"></el-checkbox>
          <el-button plain size="mini" @click="termsVisible = true"></el-button>
        </el-form-item>
        <el-form-item label="" prop="privacy" class="relative-container">
          <el-checkbox v-model="form.privacy"></el-checkbox>
          <el-button plain size="mini" @click="privacyVisible = true"></el-button>
        </el-form-item>
        <div class="form-btn">
          <el-form-item>
            <el-button native-type="submit" plain class="btn-submit"></el-button>
          </el-form-item>
        </div>
      </el-form>
    </el-card>
    <el-dialog title="" :visible.sync="termsVisible" class="signup-dialog">
      <sign-up-terms></sign-up-terms>
    </el-dialog>
    <el-dialog title="" :visible.sync="privacyVisible" class="signup-dialog">
      <sign-up-privacy></sign-up-privacy>
    </el-dialog>
  </div>
</template>

<script>
import SignUpTerms from '@/components/auth/SignUpTerms'
import SignUpPrivacy from '@/components/auth/SignUpPrivacy'

export default {
  name: 'SignUpStudent',
  data () {
    return {
      loading: false,
      termsVisible: false,
      privacyVisible: false,
      form: {
        name: '',
        email: '',
        password: '',
        passwordConfirm: '',
        phone: '',
        profile: '',
        terms: false,
        privacy: false,
        emailRecive: true,
        phoneRecive: true
      },
      rules: {
        name: [
          { required: true, message: ' .', trigger: 'blur' },
          { min: 2, max: 5, message: ' 2  5  .', trigger: 'blur' }
        ],
        email: [
          { required: true, message: ' .', trigger: 'blur' },
          { type: 'email', message: '  .', trigger: 'blur' }
        ],
        password: [
          { required: true, message: ' .', trigger: 'blur' },
          { min: 6, message: ' 6  .', trigger: 'blur' },
          { validator: this.validatePassword, trigger: 'blur' }
        ],
        passwordConfirm: [
          { required: true, message: ' .', trigger: 'blur' },
          { validator: this.validatePasswordConfirm, trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '  .', trigger: 'blur' },
          { validator: this.validatePhone, message: '   .', trigger: 'blur' }
        ],
        terms: [
          { required: true, message: ' .', trigger: 'change' },
          { validator: this.validateCheck, message: ' .', trigger: 'change' }
        ],
        privacy: [
          { required: true, message: ' .', trigger: 'change' },
          { validator: this.validateCheck, message: ' .', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    validatePassword (rule, value, callback) {
      if (value === '') {
        callback(new Error(' .'))
      } else {
        if (this.form.passwordConfirm !== '') {
          this.$refs.form.validateField('passwordConfirm')
        }
        callback()
      }
    },
    validatePasswordConfirm (rule, value, callback) {
      if (value === '') {
        callback(new Error(' .'))
      } else if (value !== this.form.password) {
        callback(new Error('  .'))
      } else {
        callback()
      }
    },
    validateCheck (rule, value, callback) {
      if (!value) {
        callback(new Error())
      } else {
        callback()
      }
    },
    validatePhone (rule, value, callback) {
      if (!/^\d{3}-\d{3,4}-\d{4}$/.test(value)) {
        callback(new Error())
      } else {
        callback()
      }
    },
    uploadProfileFile (file, fileList) {
      const isSize = file.size <= 1 * 1024 * 1024
      const isType = file.raw.type === 'image/jpg' || file.raw.type === 'image/png' || file.raw.type === 'image/jpeg' || file.raw.type === 'image/gif'

      if (isSize && isType) {
        this.form.profile = file.raw
      } else {
        fileList.pop()
        this.$message({
          showClose: true,
          message: '    .',
          type: 'warning'
        })
      }
    },
    removeProfileFile (file, fileList) {
      this.form.profile = ''
    },
    onSubmit (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          //  
          this.loading = true
          //   set
          const signUpData = {
            type: 'student',
            name: this.form.name,
            email: this.form.email,
            password: this.form.password,
            phone: this.form.phone,
            profile: this.form.profile,
            terms: this.form.terms,
            privacy: this.form.privacy,
            emailRecive: this.form.emailRecive,
            phoneRecive: this.form.phoneRecive,
            createdAt: new Date().getTime(),
            lastLoginedAt: new Date().getTime(),
            passwordChangedAt: new Date().getTime()
          }
          //  boolean
          const bool = await this.$store.dispatch('signUp', signUpData)
          //   
          if (bool) {
            this.$message({
              showClose: true,
              message: ' .',
              type: 'success'
            })
            this.$router.replace('/signin')
          }
          //  
          this.loading = false
        } else {
          return false
        }
      })
    }
  },
  components: {
    SignUpTerms,
    SignUpPrivacy
  }
}
</script>



